.card {
    border-radius: 11px;
    max-width: 300px;
    margin: 10px auto;
    background-color: rgb(17, 17, 17);
    overflow: hidden;
}

a {
    text-decoration: none;
    color: inherit;
}
.user-img {
    width: 100%;
    border-radius: inherit;
    overflow: hidden;
    height: 200px;
    object-fit: contain;
}

.user-img > img {
    width: 100%;
    border-radius: 5px;

}
.user-details {
    color: white;
    text-align: center;
    padding: 15px 0px;
}

.user-details h2 {
    font-size: 1.2rem;
    font-weight: 500;
    margin-bottom: 5px;
}

.user-details h3 {
    font-size: 0.7rem;
    font-weight: 400;
    color: gray;
}

.user-details a {
    font-size: 0.6rem;
    font-weight: 400;
    color: rgb(228, 228, 228);
    text-decoration: none;
    margin-top: 8px;
}

.connect-buttons {
    margin: 10px 0px;
    display: flex;
    justify-content: space-around;
    padding: 0px 10px;
}

.connect-buttons a {
    color: black;
    display: block;
    text-align: center;
    font-size: .9rem;
    font-weight: 500;
    padding: 5px 15px;
    background-color: white;
    border-radius: 4px;
    width: 40%;
}

.btn-linkedin {
    background-color: rgb(31, 130, 244);
}

.about-section {
    padding: 20px;
    color: white;
    border-top: 1px solid rgb(31, 31, 31)
}

.about-section h3 {
    font-size: 1.1rem;
    margin-bottom: 8px;
    letter-spacing: 1px;
}
.about-section p {
    font-size: .8rem;
    font-weight: 400;
    line-height: 1.3em;
    color: rgb(147, 147, 147);
}

.footer-section {
    
    background-color: black;
    padding: 10px;
}

.footer-section ul {
    list-style: none;
    display: flex;
    justify-content: center;
}

.footer-section a {
    margin: 0px 13px;
    border-radius: 50%;
    display: block;
    width: 20px;
    height: 20px;
    background-color: gray;
    
}